button.btn-primary {
  background-color: #fff;
  color: #000;
  &:hover {
    background-color: #fff;
    color: #000;
  }
}
.btn {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.dropdown-toggle::after {
  margin:auto 0;
}


@media (min-width: 576px) {
  .btn {
    min-width: 150px;
  }
}