table canvas {
  width: 100%;
}
table {
  text-align: center;
  th {


  }
}

.table {
  border: 1px solid #007bff !important;
  thead {
    color: #fff;
    background-color: #007bff;
    th {
      font-size: .8em;
      font-weight: 400;
      border: none;
    }
  }
  tbody {
    th {
      vertical-align: middle;
      &:not(.time) {
        font-weight: 400;
      }
    }
    th,
    td {
      border-color: #007bff;
      padding: .25em;
    }
  }
  span.arrow-wind {
    padding: 0 .3em;
  }
}
.nav-tabs .nav-link.active {
  color: #fff;
  background-color: #007bff;
}