.cssload-container{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  perspective: 975px;
  background-color: #fff;
  z-index: 100;
}
.cssload-arc {
  position: absolute;
  bottom: 50%;
  left: 50%;
  margin-left: -68px;
  width: 136px;
  height: 68px;
  overflow: hidden;
}

.cssload-arc-cube {
  position: absolute;
  bottom: -68px;
  left: 50%;
  margin-left: -68px;
  width: 136px;
  height: 136px;
  border-style: solid;
  border-top-color: transparent;
  border-right-color: #007bff;
  border-left-color: transparent;
  border-bottom-color: transparent;
  border-radius: 50%;
  box-sizing: border-box;
  animation: cssload-arc 2.3s ease-in-out infinite;
  transform: rotate(-200deg);
}


@keyframes cssload-arc {
  0% {
    border-width: 29px;
  }
  25% {
    border-width: 15px;
  }
  50% {
    transform: rotate(27deg);
    border-width: 29px;
  }
  75% {
    border-width: 15px;
  }
  100% {
    border-width: 29px;
  }
}